var exports = module.exports = {};

exports.findBootstrapEnvironment = function() {
  var envs = ["xs", "sm", "md", "lg"],
      doc = window.document,
      temp = doc.createElement("div");

  doc.body.appendChild(temp);

  for (var i = envs.length - 1; i >= 0; i--) {
    var env = envs[i];
    temp.className = "hidden-" + env;
    if (temp.offsetParent === null) {
      doc.body.removeChild(temp);
      return env;
    }
  }
  return "";
};

exports.localStorageSaveItem = function(key, value) {
  if (typeof(localStorage) != 'undefined') {
    localStorage.setItem(key, value);
  } else {
    console.error("window.localStorage, not defined");
  }
};

exports.localStorageRemoveItem = function(key) {
  if (typeof(localStorage) != 'undefined') {
    localStorage.removeItem(key);
  } else {
    console.error("window.localStorage, not defined");
  }
};

exports.localStorageGetItem = function(key) {
  if (typeof(localStorage) != 'undefined') {
    return localStorage.getItem(key);
  } else {
    console.error("window.localStorage, not defined");
  }
};



