const utils = require('./utils.js');

function clearLocalData() {
  utils.localStorageRemoveItem("ad-code");
  utils.localStorageRemoveItem("ad-email");
  utils.localStorageRemoveItem("ad-expires");
}

function loggedOutHTML() {
  $('#removeAdContent').empty();
  var content = $('<p/>', {'text': 'Please put the ad removal code below'});
  $('#removeAdContent').append(content);
  $('#removeAdContent').append(
    $('<div/>', {'style': 'margin-bottom:20px;'})
    .append(
      $('<form/>', {'id': 'removeAdsForm'})
      .append(
        $('<div/>', {'class': 'form-group'})
        .append(
          $('<label/>', {'for': 'removeAdsCodeTextbox', 'text': 'Ad Removal Code'})
        )
        .append(
          $('<input/>', {'id': 'removeAdsCodeTextbox', 'class': 'form-control input-text-xs-fix', 'type': 'text', 'name': 'ad-code', 'autocomplete': 'off', 'value': '', 'required': true})
        )
      )
      .append(
        $('<button/>', {'type': 'submit', 'data-loading-text': "<i class='fa fa-spinner fa-spin '></i> Loading", 'class': 'btn btn-primary', 'text': 'Submit'})
      )
    )
  );
}

function loggedInHTML(expires) {
  $('#removeAdContent').empty();
  var content = $('<p/>', {'text': 'You are currently logged in to the system, and ads are disabled. You code will expire on '})
  .append(
    $('<strong/>')
    .append(
      $('<span/>', {'id': 'codeExpiry', 'text': expires + ' UTC'})
    )
  );
  $('#removeAdContent').append(content);

  $('#removeAdContent').append(
    $('<p/>', {'text': 'Would you like to logout and re-enable the ads?'})
  );
  $('#removeAdContent').append(
    $('<p/>', {'class': 'help-block', 'text': 'Note: You will have to put the Ad removal code back.'})
  );
  $('#removeAdContent').append(
    $('<button/>', {'type': 'button', 'id': 'logout', 'class': 'btn btn-primary', 'text': 'Logout'})
  );
}

function checkLoginInfo() {
  var adCode = utils.localStorageGetItem("ad-code");
  var adEmail = utils.localStorageGetItem("ad-email");
  var adExpires = utils.localStorageGetItem("ad-expires");
  if (adCode && adEmail && adExpires) {
    $.post("https://wwe-champions-guide-admin.herokuapp.com/adcode",
      { code: adCode },
      function(data) {
        utils.localStorageSaveItem("ad-code", data.code);
        utils.localStorageSaveItem("ad-email", data.email);
        utils.localStorageSaveItem("ad-expires", data.expires);
        var dateFormatted = moment.utc(data.expires).format('dddd, MMMM Do YYYY, h:mm:ss a');
        loggedInHTML(dateFormatted);
      })
      .fail(function(xhr) {
        clearLocalData();
        loggedOutHTML();
      });
  } else {
    loggedOutHTML();
  }
}

$(document).ready(function () {
  checkLoginInfo();

  $(document).on('click', '#logout', function(e){
    console.log("Test");
    clearLocalData();
    window.location = "/";
  });

  $('#removeAdsForm').on('submit', function(e) {
    e.preventDefault();
    $('button[type="submit"]').button('loading');
    var data = $(this).serializeArray().reduce(function(obj, item) {
      obj[item.name] = item.value;
      return obj;
    }, {});

    $.post("https://wwe-champions-guide-admin.herokuapp.com/adcode",
      { code: data['ad-code'] },
      function(data) {
        console.log("success");
        $('#modalSuccess').modal();
        utils.localStorageSaveItem("ad-code", data.code);
        utils.localStorageSaveItem("ad-email", data.email);
        utils.localStorageSaveItem("ad-expires", data.expires);
        // console.log(data);
      })
      .fail(function(xhr) {
        console.log("error");
        $('#modalError').modal();
        clearLocalData();
        // console.log(xhr.responseJSON);
      })
      .always(function() {
        $('button[type="submit"]').button('reset');
      });
  });
});



